@import url(https://fonts.googleapis.com/css2?family=Tenali+Ramakrishna&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* overflow-x: hidden; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.see-all {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}
.see-all-text {
  font-weight: bold;
  color: rgb(0, 140, 255);
  cursor: pointer;
  margin: 10px 50px;
  padding: 8px 15px;
  background-color: rgba(216, 216, 216, 0.246);
  border-radius: 5px;
}
.see-all-text:hover {
  background-color: rgba(185, 185, 185, 0.322);
  color: rgb(3, 124, 223);
}

@font-face {
  font-family: "Tamil";
  src: url(/static/media/tamil.a26c7dbc.TTF);
}

.title-nav {
  position: relative;
  display: flex;
  flex-direction: column;
}

.company-name {
  color: rgb(51, 51, 51);
  font-size: 40px;
  font-weight: 700;
  font-family: "Tenali Ramakrishna", sans-serif;
  padding: 0;
  margin: 0;
  line-height: 40px;
}
.tamil {
  position: absolute;
  top: 75%;
  left: 50%;
  -webkit-transform: translate(-50%);
          transform: translate(-50%);
  font-family: "Tamil";
  font-size: 15px;
  color: black;
  font-weight: bold;
}

.home-container {
  height: 50vh;
  width: 100%;
  background-color: black;
}
.bg-img {
  background-color: rgb(0, 0, 0);
  height: 100%;
  width: 100%;
  position: relative;
}
.bg-img img {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
}
.bg-img .title {
  font-size: 40px;
  font-weight: bold;
  color: white;
  letter-spacing: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
@media (max-width: 500px) {
  .bg-img .title {
    font-size: 25px;
  }
  .home-container {
    height: 20vh;
    width: 100%;
    background-color: black;
  }
}
@media (max-width: 800px) {
  .home-container {
    height: 30vh;
    width: 100%;
    background-color: black;
  }
}

.section-title {
  font-size: 30px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
}
.section-title::after {
  content: "";
  font-weight: bold;
  height: 5px;
  width: 100px;
  background-color: black;
}

.about-us {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about-us-all-content {
  width: 70%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgb(245, 245, 245);
  border-radius: 10px;
  padding: 20px;
}
.about-us-content {
  font-size: 20px;
  color: rgb(65, 65, 65);
  width: 95%;
  text-align: justify;
}
@media (max-width: 750px) {
  .about-us-all-content {
    width: 95%;
  }
}

.books-div {
  padding-top: 80px;
}
.all-books {
  display: flex;
  overflow-x: scroll;
}
.all-books::-webkit-scrollbar {
  /* display: none; */
  /* scroll-behavior: smooth; */
}
.book-item {
  padding: 50px;
}

/* Book */
.book-container {
  background-color: whitesmoke;
  display: inline-block;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  margin: 10px 10px;
  border-radius: 10px;
  overflow: hidden;
  width: 300px;
}
.book-link {
  -webkit-user-drag: none;
  /* user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none; */
}
.book {
  height: 400px;
  width: 300px;
  overflow: hidden;
}
.book img {
  width: 100%;
  height: auto;
  transition: 0.35s;
  -webkit-transform: scale(1);
          transform: scale(1);
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.book img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.book-title {
  font-size: 22px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 500;
  text-align: center;
  margin: 10px 0;
  color: rgb(112, 112, 112);
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  width: 100%;
}

.videos-div {
  padding-top: 80px;
}

.all-videos {
  display: flex;
  overflow-x: auto;
  /* justify-content: center; */
}
/* video */
.video-container {
  display: inline-block;
  justify-content: right;
  background-color: white;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.418);
  margin: 10px;
  width: 400px;
}
.video-link {
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.thumbnail {
  height: 220px;
  width: 400px;
  overflow: hidden;
}
.thumbnail img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  object-fit: contain;
  transition: 0.35s;
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.thumbnail:hover > img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.video-title {
  padding: 0 5px;
  display: flex;
  align-items: center;

  height: 70px;
  font-size: 22px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", "Liberation Sans", sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-weight: 500;
  text-align: start;
  margin: 10px 0;
  color: rgb(112, 112, 112);
}

.article-div {
  padding-top: 80px;
}
.all-articles {
  padding: 20px;
}

/* article */

.to-link:hover {
  text-decoration: none;
}

.article-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: auto;
  width: 350px;
  background-color: #ecf0f1;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.527);

  transition: 0.25s;
  overflow: hidden;
}
.article-container:hover {
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.527);
}
.article-img {
  position: relative;
  height: 250px;
  width: auto;
  overflow: hidden;
  background-color: rgb(212, 212, 212);
}

.article-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}
.article-all-content {
  background-color: white;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.auth-info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  width: 100px;
}
.profile-pic {
  height: 70px;
  width: 70px;
  background-color: rgb(214, 214, 214);
  border-radius: 50%;
  border: 5px solid white;
  -webkit-transform: translateY(-35%);
          transform: translateY(-35%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
.profile-pic img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.auth-name {
  width: 300px;
  text-align: center;
  color: gray;
  font-size: 18px;
  margin-top: -20px;
}
.article-main-content {
  padding: 5px 5px;
  text-align: center;
  height: 200px;
  overflow: hidden;
}
.article-title {
  text-align: start !important;
  display: inline;
  color: rgb(58, 58, 58);
  font-size: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Arial, "Noto Sans";
  font-weight: bold;
}
.article-subtitle {
  padding: 0 10px;
  text-align: justify;
  font-family: Arial, "Noto Sans";
  font-size: 18px;
  color: rgb(110, 110, 110);
}
.read-more {
  color: white;
  cursor: pointer;
  font-size: 20px;
  padding: 10px;
  width: 100%;
  background-color: rgb(0, 132, 255);
  text-align: center;
}

/* see all wrap */

.saa-wrap {
  display: flex;
  flex-wrap: wrap;
  text-align: start;
  justify-content: center;
}

.querys-div {
  padding: 80px 0;
}
.all-querys {
  display: flex;
  overflow: scroll;
  padding: 15px;
}
.q-padding {
  padding: 20px;
}

/* query */
.query-container {
  height: auto;
  width: 500px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.267);
  text-align: start;
  padding: 15px;
  margin: 0 10px;
}
.image {
  width: 100%;
  height: 300px;
  overflow: hidden;
}

.image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  cursor: pointer;
  -webkit-user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.question {
  color: #0489b4;
  font-size: 22px;
  font-weight: bold;
}
.answer {
  font-size: 18px;
  color: #6e6e6e;
}
.author {
  display: flex;
  align-items: center;
  margin-top: 15px;
  height: 50px;
  width: 100%;
}
.author-img {
  margin-right: 15px;
  height: 40px;
  width: 40px;
  background-color: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.267);
  border-radius: 50px;
}
.author-img img {
  height: 40px;
  width: 40px;
  object-fit: cover;
}
.author-name {
  color: rgb(87, 87, 87);
  font-size: 25px;
  font-family: "Tenali Ramakrishna", sans-serif;
}

.book-preview {
  display: flex;
  align-items: center;

  overflow: hidden;
}
.book-image {
  margin: 20px;
  /* height: 10%; */
  width: 400px;
  background-color: white;
  object-fit: contain;
  border-radius: 20px;
  overflow: hidden;
}
.book-image .img {
  width: 100%;
  height: auto;

  object-fit: cover;
}

.bp-content {
  text-align: justify;
}

.bp-col {
  display: flex;
  justify-content: center;
  align-items: center;
}
.book-preview-content-col {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: start;
}
.bp-c {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /* justify-content: start; */
}
.bp-title {
  font-size: 35px;
  font-weight: bold;
}
.bp-content {
  font-size: 25px;
}
.bp-read-more {
  color: rgb(0, 81, 255);
  cursor: pointer;
}
.bp-like {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
  font-size: 20px;
  color: gray;
}
.heart-color,
.share-color {
  width: 50px;

  cursor: pointer;
  transition: 0.1s;
}
.heart-color {
  color: rgb(238, 15, 71);
}

.share-color {
  color: rgb(0, 81, 255);
}
.heart-color:hover,
.share-color:hover {
  font-size: 30px;
}
.bp-buttons {
  margin-top: 20px;
  display: flex;
  width: 400px;
  justify-content: space-between;
}
.bp-buttons:hover Link {
  text-decoration: none;
}
.bp-button {
  cursor: pointer;
  background-color: rgb(4, 163, 4);
  color: white;
  padding: 10px 50px;
  border-radius: 5px;
  font-size: 20px;
  font-weight: bold;
  transition: 0.5s;
}
.bp-button:hover {
  background-color: rgb(2, 102, 2);
}
.bp-button.download {
  background-color: rgb(16, 121, 241);
}
.bp-button.download:hover {
  background-color: rgb(0, 82, 177);
}

.video-player {
  display: flex;
  transition: 0.5s;
}
.video-content {
  display: flex;
  justify-content: center;
  background-color: rgb(243, 243, 243);
  width: 100%;
}
.vp-video-div {
  padding: 20px;
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-around;
}
.vp-video-title {
  text-align: start;
  font-size: 30px;
}
.vp-video-description {
  font-size: 20px;
  text-align: justify;
}
.vp-video {
  height: calc(4vw * 9);
  width: calc(4vw * 16);
  padding-bottom: 20px;
  border-bottom: 3px solid rgb(212, 212, 212);
  margin-bottom: 10px;
}

/* rc video */

.rc-video {
  width: 1000px;
}
.vp-video-list {
  width: 100%;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  background-color: rgb(243, 243, 243);
  margin: 10px 0;
  transition: 0.5s;
  padding: 10px;
}
.vp-video-list:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.486);
}
.vp-vl-video {
  height: 100px;
  width: 250px;
  overflow: hidden;
  background-color: rgb(207, 207, 207);
  /* margin-bottom: 10px; */
}
.vp-vl-video-img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.vp-vl-video-title {
  padding-left: 10px;
  text-align: start;
  width: 100%;
  font-size: 18px;
}
.video-link:hover {
  text-decoration: none;
}
@media (max-width: 1000px) {
  .video-content {
  }
  .video-player {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .vp-video {
    /* height: 60vw; */
    height: calc(6vw * 9);
    width: 100%;
  }
  .rc-video {
    width: 100%;
    padding: 20px;
  }
}

.player-wrapper {
  width: auto;
  height: auto;
}
.react-player {
  position: relative;
}

.react-player > div {
  position: absolute;
}

.av-article-img-div {
  width: 100%;
  height: auto;
  background-color: rgb(243, 243, 243);
  overflow: hidden;
  align-items: center;
  display: flex;
  padding: 20px;
}
.av-article-img-div img {
  object-fit: cover;
  width: auto;
  margin-right: 20px;
}
.av-article-title {
  font-size: 35px;
  text-align: justify;
}
.av-article-content {
  padding: 30px 20px;
  font-size: 20px;
  text-align: justify;
}

.query-read {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}
.main-query {
  /* padding: 50px; */
}
.queryImage {
  height: 50vh;
  width: 100%;
  background-color: yellow;
  overflow: hidden;
}
.queryImage img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.postDb-title {
  font-size: 30px;
  font-weight: bold;
}
.postDb-content {
  padding: 5px;
}
.querys {
  margin: 5px;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.383);
}
.rq-q-image {
  min-width: 160px;
  max-width: 100px;
  height: 100px;
  width: 160px;
  padding: 5px;
  display: inline-block;
}
.rq-q-image img {
  object-fit: cover;
}
.rq-q-title {
  /* width: 50%; */
  display: inline-block;
}
.list-of-query {
  background-color: rgb(243, 243, 243) !important;
}

@media (max-width: 500px) {
  .rq-q-image {
    min-width: 130px;
    max-width: 70px;
    height: 70px;
    width: 130px;
    padding: 3px;
    display: inline-block;
  }
}

